"use client"

import { useState } from "react"
import Sidebar from "@/components/_sidebar/sidebar-navigation"
import MainContent from "@/components/_sidebar/sidebar-navigation"

export default function Page() {
  // Estado controlado na página pai
  const [activeItem, setActiveItem] = useState("home")
  const [isExpanded, setIsExpanded] = useState(false)

  const handleItemClick = (itemId: string) => {
    setActiveItem(itemId)
    // Fecha o menu no mobile após seleção
    if (window.innerWidth < 768) {
      setIsExpanded(false)
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-emerald-400 via-emerald-500 to-emerald-900 relative overflow-hidden">
      {/* Background decoration dots */}
      <div className="absolute inset-0 pointer-events-none">
        <div className="absolute top-20 left-1/4 w-1 h-1 bg-white/20 rounded-full"></div>
        <div className="absolute top-32 right-1/3 w-1 h-1 bg-white/20 rounded-full"></div>
        <div className="absolute top-16 right-1/4 w-1 h-1 bg-white/20 rounded-full"></div>
        <div className="absolute bottom-32 left-1/3 w-1 h-1 bg-white/20 rounded-full"></div>
      </div>

      {/* Sidebar - componente visual sem lógica */}
      <Sidebar
        isExpanded={isExpanded}
        setIsExpanded={setIsExpanded}
        activeItem={activeItem}
        onItemClick={handleItemClick}
      />

      {/* Main Content */}
      <MainContent activeItem={activeItem} isExpanded={isExpanded} />
    </div>
  )
}
