"use client"

import { useState } from "react"
import { Home, Ruler, BarChart3, FileDown, FileText, Leaf, ChevronLeft, Settings, Menu } from "lucide-react"
import { Button } from "@/components/ui/button"

const menuItems = [
  { icon: Home, label: "Home", id: "home" },
  { icon: Ruler, label: "Measure", id: "measure" },
  { icon: BarChart3, label: "Analyze", id: "analyze" },
  { icon: FileDown, label: "Reduce", id: "reduce" },
  { icon: FileText, label: "Report", id: "report" },
]

export default function Component() {
  const [isExpanded, setIsExpanded] = useState(false)
  const [activeItem, setActiveItem] = useState("home")

  return (
    <div className="min-h-screen bg-gradient-to-br from-emerald-400 via-emerald-500 to-emerald-900 relative overflow-hidden">
      {/* Background decoration dots */}
      <div className="absolute inset-0">
        <div className="absolute top-20 left-1/4 w-1 h-1 bg-white/20 rounded-full"></div>
        <div className="absolute top-32 right-1/3 w-1 h-1 bg-white/20 rounded-full"></div>
        <div className="absolute top-16 right-1/4 w-1 h-1 bg-white/20 rounded-full"></div>
      </div>

      {/* Mobile header */}
      <div className="md:hidden flex items-center justify-between p-4">
        <Button
          variant="ghost"
          size="icon"
          onClick={() => setIsExpanded(!isExpanded)}
          className="text-white hover:bg-white/10"
        >
          <Menu className="h-6 w-6" />
        </Button>
        <Button variant="ghost" size="icon" className="text-white hover:bg-white/10">
          <Settings className="h-6 w-6" />
        </Button>
      </div>

      {/* Sidebar */}
      <div
        className={`
        fixed md:relative top-0 left-0 h-full z-50
        transition-all duration-300 ease-in-out
        ${isExpanded ? "w-64" : "w-16 md:w-20"}
        ${isExpanded ? "translate-x-0" : "-translate-x-full md:translate-x-0"}
      `}
      >
        <div className="h-full bg-emerald-500/80 backdrop-blur-sm border-r border-white/10 rounded-r-3xl md:rounded-r-none md:rounded-3xl m-2 md:m-4">
          <div className="flex flex-col h-full p-4">
            {/* Logo */}
            <div className="flex items-center justify-center mb-8 mt-4">
              <div className="w-12 h-12 bg-white/20 rounded-full flex items-center justify-center backdrop-blur-sm">
                <Leaf className="h-6 w-6 text-white" />
              </div>
            </div>

            {/* Toggle button - only visible when expanded */}
            {isExpanded && (
              <div className="flex justify-end mb-4">
                <Button
                  variant="ghost"
                  size="icon"
                  onClick={() => setIsExpanded(false)}
                  className="text-white hover:bg-white/10 h-8 w-8"
                >
                  <ChevronLeft className="h-4 w-4" />
                </Button>
              </div>
            )}

            {/* Navigation items */}
            <nav className="flex-1 space-y-2">
              {menuItems.map((item) => {
                const Icon = item.icon
                const isActive = activeItem === item.id

                return (
                  <button
                    key={item.id}
                    onClick={() => setActiveItem(item.id)}
                    className={`
                      w-full flex items-center gap-3 p-3 rounded-xl
                      transition-all duration-200
                      ${isActive ? "bg-white text-emerald-600 shadow-lg" : "text-white hover:bg-white/10"}
                      ${!isExpanded ? "justify-center" : ""}
                    `}
                  >
                    <Icon className="h-5 w-5 flex-shrink-0" />
                    {isExpanded && <span className="font-medium text-sm">{item.label}</span>}
                  </button>
                )
              })}
            </nav>

            {/* Settings button at bottom */}
            <div className="mt-auto">
              <button
                className={`
                  w-full flex items-center gap-3 p-3 rounded-xl
                  text-white hover:bg-white/10 transition-all duration-200
                  ${!isExpanded ? "justify-center" : ""}
                `}
              >
                <Settings className="h-5 w-5 flex-shrink-0" />
                {isExpanded && <span className="font-medium text-sm">Settings</span>}
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* Main content area */}
      <div
        className={`
        flex-1 transition-all duration-300 ease-in-out
        ${isExpanded ? "md:ml-72" : "md:ml-28"}
        p-6
      `}
      >
        <div className="text-white">
          <h1 className="text-2xl font-bold mb-4">Dashboard</h1>
          <p className="text-white/80">Conteúdo principal da aplicação apareceria aqui.</p>
        </div>
      </div>

      {/* Overlay for mobile */}
      {isExpanded && (
        <div
          className="md:hidden fixed inset-0 bg-black/20 backdrop-blur-sm z-40"
          onClick={() => setIsExpanded(false)}
        />
      )}
    </div>
  )
}
